@mixin positionCenter {
  position: absolute;
  top: $procent-fifty;
  left: $procent-fifty;
  transform: translate(-$procent-fifty, -$procent-fifty);
}

@mixin positionBig {
  position: absolute;
  bottom: 50%;
  transform-origin: bottom;
}

@mixin positionSmall {
  position: absolute;
  bottom: 50%;
  transform-origin: bottom;
}
